@media print {
  #fresherTemplate1 {
    width: 794px; /* A4 width in pixels at 72 DPI */
  }
}

#fresherTemplate1{
	padding: 1.5vw;
}

.model-hd-head{
	background-color: #12343b;
}
.model-hd-head h5{
	font-size: 3vw;
	color: white;
}
.model-hd-head button{
	background-color: inherit;
}
.model-hd-head button span{
	font-size: 5vw;
	color: white;
	padding: 0px;
	background-color: inherit;
}

.model-hd-ft{
	background-color: #12343b!important;
}
.model-hd-ft>button{
	font-size: 2vw;
	padding: 1vw 2vw;
	color: white;
	font-family: sans-serif;
	background-color: inherit;
}


/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
  .model-hd-head h5{
	font-size: 4vw;
	}
	.model-hd-head button span{
	font-size: 5vw;
	}

	.model-hd-ft>button{
	font-size: 5vw;
  padding: 2vw 4vw;
	}

}


/*small*/
@media screen and (min-width:577px) and (max-width:767px){
  .model-hd-head h5{
	font-size: 4vw;
	}
	.model-hd-head button span{
	font-size: 5vw;
	}
	.model-hd-ft>button{
	font-size: 4.5vw;
  padding: 2vw 4vw;
	}
}
