
/*logo header------------------------------------*/
.logo-header-outer{
	background-color:#12343b ;
	padding: 1vw 0vw;
	position: sticky;
	top: 0;
	z-index: 2;
}
.logo-header-outer h1{
	font-size: 3vw;
	font-weight: bold;
	color: #fecb88;
}
.logo-header-outer h1 span{
	cursor: pointer;
}

/*create heading-------------------------------------*/
.resume-hd-box-img.create-img-box img{
	animation: none;
}
.complete-heading-box{
	background-color: #f6dec8;
	border-radius: 15vw 0% 15vw 0%;
	overflow: hidden;
	padding: 4vw 0vw 2vw 0vw;
}

.complete-rs-heading h1{
	font-size: 5vw;
	text-align: left;
	font-family: 'Poppins',sans-serif;
	letter-spacing: 1px;
	color:#12343b;
	font-weight: bold;
	padding:0vw 0vw 0vw 8.2vw!important;
	margin: 0vw;
}
.complete-rs-heading p{
	padding:2vw 0vw 2vw 8.2vw!important;
	text-align: left;
	font-size: 2vw;
	font-weight: 500;
	color: blue;
  	text-shadow: 1px 1px 10px white;
	font-family: sans-serif;
	letter-spacing: .1vw;
}
.complete-rs-heading button{
	background-color: #ff424d;
  border-radius: 2vw;
  border: none;
  color: white;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 1.5vw;
  margin: 1vw 0vw 0vw 8.2vw;
  padding: 1vw 3vw;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  transition: .3s;
}
.complete-rs-heading button:hover{
	color: #12343b;
	box-shadow: #12343b 2px 2px 0 0;
}

/*create steps------------------------------------------------------*/
.create-steps-div{
	padding: 6vw 0vw;
}
.create-steps-icon-div{
/*	overflow: hidden;*/
    width:10vw;
    height: 10vw;
    margin: auto;
    border-radius: 100%;
    background-color: #12343b;
    background-color: white;
/*    background-color: #fecb88;*/
    background-position: center;
    background-repeat: no-repeat;
    background-size: 2.5vw;
    transition:.2s linear;
    cursor: pointer;
    position: relative;
}
.create-steps-icon-div:hover{
    background-color: #fecb88;
    background-size: 3.5vw;
}
.create-steps-heading{
	text-align: center;
	  margin: 1vw 0vw 0vw 0vw;
	  font-size: 1.4vw;
	  color: white;
}
.create-steps-icon-div.steps-count-icon-temp::after,
.create-steps-icon-div.steps-count-icon-write::after {
  content: ""; /* Applies only to pseudo-elements (::after) */
  display: inline-block;
  height: 8vw;
  width: 15vw;
  background-color: blue;
  background-image: url("../img/rArrow.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-origin: center;
  position: absolute;
  left: 150%;
  top: 10%;
}



/*form---------------------------------------------------*/
.create-form-box{
	background-color: #fecb88;
	padding: 8vw 0vw 8vw 0vw;
	border-radius: 15vw 0% 15vw 0%;
}

.create-form-box h1{
	font-size: 4vw;
	font-weight: bold;
}

.create-form-box p{
	font-size: 1.5vw;
	font-family: 'Poppins',sans-serif;
	text-align: justify;
	color: blue;
  	text-shadow: 1px 1px 1px white;
}

.create-form-box h2{
	color: 12343b;
	margin-top: 5vw;
	margin-bottom: 1vw;
	font-weight: bold;
	font-size: 3vw;
}
.create-form-box form label{
	width: 100%;
	font-weight: 500;
	font-size: 1.2vw;
	margin: 0vw 0vw 1vw 0vw;
}
.create-form-box form input{
	
	padding: .8vw 1vw;
	font-weight: bold;
	font-size: 1.3vw;
	color: blue!important;
	border-radius: 0px;
}
.create-form-box form input:focus{
	box-shadow: none;
	outline: .2vw solid blue;
}

.create-form-box form textarea{
	padding: .8vw 1vw;
	margin: 0vw 0vw 1vw 0vw;
	font-weight: bold;
	color: blue!important;
	font-size: 1.3vw;
	border-radius: 0px;
}
.create-form-box form textarea:focus{
	box-shadow: none;
	outline: .2vw solid blue;
}

.create-form-box form select{
	padding: .8vw 1vw;
	margin: 0vw 0vw 1vw 0vw;
	font-weight: bold;
	color: blue!important;
	font-size: 1.3vw;
	border-radius: 0px;
}
.create-form-box form select:focus{
	box-shadow: none;
	outline: .2vw solid blue;
}

.create-form-box form button{
	padding: .8vw 1.4vw;
	margin: .5vw 0vw;
	border: none;
	font-size: 1.5vw;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4), 0px 7px 13px -3px rgba(0, 0, 0, 0.3), inset 0px -3px 0px rgba(0, 0, 0, 0.2);
}
.remove-btn{
	width: 25%;
}
.add-btn{
	width: 50%;
}
.create-reset-btn{
	padding: .8vw 1.4vw;
	margin: .5vw 0vw;
	width: 100%;
	font-weight: bold;
	border: none;
	font-size: 2vw;
	color: #12343b;
	margin: 2vw 0vw;
	background-color: yellow;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4), 0px 7px 13px -3px rgba(0, 0, 0, 0.3), inset 0px -3px 0px rgba(0, 0, 0, 0.2);
}

/*resume triger btn--------------------------------------------------------------------------*/
.resume-triger-box{
	background-color: #12343b;
	padding: 4vw 8vw;
}
.resume-triger-box h1{
	font-size: 3.8vw;
	color: #fecb88;
	font-weight: bold;
	margin-bottom: 3vw;
}

.resume-triger-btn{
	border:none ;
	width: 100%;
	font-size: 2.2vw;
	font-family: 'Roboto',sans-serif;
	padding: 3vw 0vw;
	margin: 1vw 0vw;
	background-color: #fecb88;
	font-weight: bold;
	border-radius: 5%;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4), 0px 7px 13px -3px rgba(0, 0, 0, 0.3), inset 0px -3px 0px rgba(0, 0, 0, 0.2);
}

.template-btn-box-hd{
	font-size: 3vw;
	font-family: Poppins;
	color: white;
}



